	import uniIcon from '@/components/uni-icon/uni-icon.vue'
	import http from '../../store/http.js'
	export default {
			
		components: {
			uniIcon
		},
		data() {
			return {
				searchResult:[],
				isSearch:false,
				test:'aaaaa',
				searchinput:'',
				deptList:[],
				categoryList: [],
				subCategoryList: [],
				height: 0,
				categoryActive: 0,
				scrollTop: 0,
				scrollHeight: 0,
				name: "七月_"
			}
		},
		methods: {
			async searchdept(){
				if(this.searchinput==null || this.searchinput==''){
					this.isSearch=false	
					return
				}
				for(var i=0;i<this.categoryList.length;i++){
					let d=this.categoryList[i].departments
					for(var j=0;j<d.length;j++){
						if(d[j].name.indexOf(this.searchinput)>=0){
							d[j].index=d[j].id
							this.searchResult.push(d[j])
						}
					}
				}
				this.isSearch=true
			},
			scroll(e) {
				this.scrollHeight = e.detail.scrollHeight;
			},
			categoryClickMain(categroy, index) {
				this.categoryActive = index;
				this.subCategoryList = categroy.departments;
				this.scrollTop = -this.scrollHeight * index;
			},
			async getCategory() {
				
				
				let opts={
                 	url: '/department_kind/getClinical',
                    method: 'GET'
	             };
	             let param={};
				
			    http.httpTokenRequest(opts, param).then(res => {
					this.categoryList=res.data.data
					this.subCategoryList=this.categoryList[0].departments
	            },error => {
				uni.showToast({
					title: '网络错误，请稍后重试',
					duration: 2000,
					icon:'none'
				})
					});  
			}	
		},
		onLoad: function () {
			this.getCategory();
			this.height = uni.getSystemInfoSync().windowHeight;
		}
	}
